	module.exports = {"data":{"markdownRemark":{"html":"<h3>Create a container with a mongo database and connect to it</h3>\n<p>Start with displaying all currently running containers:</p>\n<pre><code class=\"language-bash\">docker ps\n</code></pre>\n<p>Then create locally the container associated with the mongo docker image:</p>\n<pre><code class=\"language-bash\">docker run -p 27017:27017 -d mongo\n</code></pre>\n<p>Executing another time <code>docker ps</code> results in information on the created container like the following being displayed:</p>\n<pre><code>CONTAINER ID        IMAGE               COMMAND                  CREATED             STATUS              PORTS                      NAMES\n2c6a61aba41b        mongo               \"docker-entrypoint.s…\"   3 minutes ago       Up 3 minutes        0.0.0.0:27017->27017/tcp   clever_dubinsky\n</code></pre>\n<p>Connection to the database at url localhost:27017 can be opened using for instance the robo-3t nosql database editor.\nFinally, the container when not needed anymore can be stopped with:</p>\n<pre><code>docker stop 2c6a61aba41b\n</code></pre>","frontmatter":{"title":"Docker Recipes","date":"March 05, 2019","path":"/docker","tags":["docker","container","devops"],"excerpt":"Some Docker Recipes"}}},"pathContext":{"prev":{"html":"<h3>General documentation</h3>\n<p><a href=\"https://facebook.github.io/react-native/docs/getting-started.html\">Quickly get started (with create-react-native-app)</a></p>\n<p><a href=\"https://facebook.github.io/react-native/docs/tutorial.html\">Keep the official docs in mind</a></p>\n<p><a href=\"https://github.com/react-community/create-react-native-app\">The create-react-native-app Github repo</a></p>\n<h3>Styling</h3>\n<p><a href=\"https://github.com/vhpoet/react-native-styling-cheat-sheet\">On react native supported style properties</a></p>\n<p><a href=\"https://css-tricks.com/snippets/css/a-guide-to-flexbox/\">On flexbox in general (not specifically in React Native)</a></p>\n<p><a href=\"https://facebook.github.io/react-native/docs/images.html\">More about images</a></p>\n<h3>Navigation</h3>\n<p><a href=\"https://github.com/wix/react-native-navigation\">React Native Navigation</a></p>","id":"/home/nperon/Documents/workshop/src/pages/2019-02-18-react-native/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2019-01-01T22:09:20.235Z","path":"/react-native","title":"React Native","excerpt":"Documentation on React Native","tags":["react native","mobile app"]}},"next":{"html":"<h3>General documentation</h3>\n<h3>Bootstrap</h3>\n<p><a href=\"https://loiane.com/2017/08/how-to-add-bootstrap-to-an-angular-cli-project/\">How to Add Bootstrap to an Angular CLI project</a></p>","id":"/home/nperon/Documents/workshop/src/pages/2019-03-05-styling/index.md absPath of file >>> MarkdownRemark","frontmatter":{"date":"2019-03-05T22:35:12.235Z","path":"/style","title":"Style","excerpt":"Documentation on style","tags":["style","css","bootstrap"]}}}}

